/* button-reset-invoice */
.button-reset-invoice {
    position: relative;
    top: 195px;
    left: 600px;
    background: url('../images/reset.svg');
    background-color: rgba(255, 0, 0, 0);
    border: none;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
    transition: 0.3s;
    opacity: 0.2;
    z-index: 1 !important;
  }
  
  .button-reset-invoice span {
    display: none;
  }
  
  .button-reset-invoice:hover {
    cursor: pointer;
    opacity: 1;
  }

